@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Barlow Semi Condensed', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.pieceBackground {
  background-color: blue;
}
.piece {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 10px;
}
.piece-empty {
  border: 2px solid white;
  background-color: black;
}

.piece-player {
  border: 2px solid white;
  background-color: red;
}

.piece-AI {
  border: 2px solid white;
  background-color: gold;
}

.piece-selector {
  width: 40px;
  height: 40px;
}

.piece-selector:hover {
  background-color: red;
  border: 2px solid white;
  cursor: pointer;
}

.winningPiece {
  border: 4px solid #00ff37 !important;
}


@media only screen and (max-width: 430px) {
  .piece {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin: 7px;
  }
}
@media only screen and (max-width: 350px) {
  .piece {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: 5px;
  }
}
.main {
  width: 650px;
  margin: auto;
  margin-top: 7vh;
  background-color: black;
  border-radius: 12px 12px;
  box-shadow: 5px 15px 18px #adadad;
}

.pieceRow {
  display: flex;
  justify-content: center;
  margin: auto;
  width: 420px;
}
.head {
  margin: auto;
  cursor: default;
  background-color: #fed766;
  
  width: 100%;
  border-radius: 10px 10px 0 0;
}

.foot {
  margin: auto;
  cursor: default;
  background-color: black;
  height: 70px;
  width: 100%;
  border-radius: 0 0 10px 10px;
  font-family: Futura, Trebuchet MS, Arial, sans-serif;
  color: black;
  padding: 15px;
  font-size: 20px;
  font-weight: bold;
}
.title {
  font-family: Futura, Trebuchet MS, Arial, sans-serif;
  color: black;
  padding: 15px;
  font-size: 20px;
  font-weight: bold;
}

.board {
  background-color: black;
  width: 420px;
  margin: auto;
  transform: rotateX(180deg);
  
}

.btn {
  font-family: Futura, Trebuchet MS, Arial, sans-serif;
}

.difficultyGroup {
  margin-bottom: 10px;
}

.difficultyButton {
  font-family: Futura, Trebuchet MS, Arial, sans-serif;
  font-size: 12px;
  font-weight: bold;
  display: inline-block;
  margin: 10px;
  cursor: pointer;
}

.difficultyButton:hover {
  text-decoration: underline;
}

.difficultyButtonSelected {
  font-family: Futura, Trebuchet MS, Arial, sans-serif;
  font-size: 12px;
  font-weight: bold;
  display: inline-block;
  margin: 10px;
  cursor: pointer;
  text-decoration: underline;
}

.newGameButton {
  color: white;
  font-family: Futura, Trebuchet MS, Arial, sans-serif;
  font-size: 12px;
  font-weight: bold;
  display: inline-block;
  cursor: pointer;
  padding: 6px;
  border-radius: 2px;
  background-color: #17a2b8;
}

.newGameButton:hover {
  background-color: #1c8d9e;
}

.newGame {
  padding-bottom: 20px;
  padding-top: 5px;
}

@media only screen and (max-width: 650px) {
  
  
  .main {
    width: 100%;
    border-radius: 0;
  }
  .head {
    width: 100%;
    border-radius: 0;
  }
  .board{
    width: 100%;
    height: 50%;
  }
  .pieceRow{
    width: 100%;
  }
  .foot{
    width: 100%;
    border-radius: 0;
  }
  
}

@media only screen and (max-width: 430px) {
  .main{
    margin-top: 0;
    border-radius: 0;
    box-shadow: none;
    height: 100vh;
  }
  .foot{
    border-radius: 0;
  }
  .head{
    border-radius: 0;
  }
}

